import { getEmpty, solve, isLoaded, getIndexByPosition, canMove, swapClicked } from '../helpers/tiles';

export default function (state = {}, action) {
  switch (action.type) {
    case 'MOVE_TILE':
      return {
        ...state,
        tiles: swapClicked(state.tiles, getIndexByPosition(state.tiles, action.payload.position), getEmpty(state), state.size)
      }
    case 'SOLVE':
      let solved = solve(state);
      return {
        ...state,
        tiles: solved.tiles,
        finished: solved.solved
      };
    case 'TILE_LOADED':
      return {
        ...state,
        tilesLoaded: state.tilesLoaded + 1,
        loaded: isLoaded(state.tiles, state.tilesLoaded + 1)
      };
    default:
      return state;
  }
}


