import '../styles/components/Tile.css';
import PropTypes from 'prop-types';
import React from 'react';
import TweenMax from 'gsap'

class Tile extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      col: 0,
      row: 0
    };

    this.onClick = this.onClick.bind(this);
  }

  componentDidMount() {
    this.loadImage();
    TweenMax.set(this.el, { left: this.props.col * 124, top: this.props.row * 124 });
  }

  loadImage() {
    let image = new Image();
    image.onload = () => this.props.tileLoaded();
    image.src = `./images/puzzle/${this.props.value + 1}.jpg`;
  }

  onClick(e) {
    if (this.props.canMove(this.props)) {
      this.props.moveTile(this.props);
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.col !== prevProps.col || this.props.row !== prevProps.row) {
      TweenMax.to(this.el, 0.35, { left: this.props.col * 124, top: this.props.row * 124, ease: Expo.easeOut });
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.startIntro !== nextProps.startIntro) {
      this.intro();
    }
  }

  intro() {
    let index = this.props.position;
    TweenMax.to(this.el, 0.3 + (index * 0.05), { y: 0, force3D: true, ease: Quad.easeInOut, delay: index * 0.05 });
  }

  render() {
    return (
      <a ref={element => this.el = element} onClick={this.onClick} className={`tile ${this.props.isEmpty ? 'empty' : ''}`}>
        <img alt='tile' src={`./images/puzzle/${this.props.value + 1}.jpg`} />
      </a>
    )
  }
}

Tile.propTypes = {
  col: PropTypes.number.isRequired,
  row: PropTypes.number.isRequired,
  position: PropTypes.number.isRequired,
  startIntro: PropTypes.bool.isRequired,
  isEmpty: PropTypes.bool.isRequired,
  value: PropTypes.number.isRequired,
  tileLoaded: PropTypes.func.isRequired,
  canMove: PropTypes.func.isRequired,
  moveTile: PropTypes.func.isRequired
}

export default Tile;


