export const setCol = (position, size) => {
  const rest = (position + 1) % size;
  return rest === 0 ? size - 1 : rest - 1;
}

export const setRow = (position, size) => {
  return (Math.ceil((position + 1) / size)) - 1;
}

export const setEmptyState = (value, total) => {
  return value === total - 1;
}


