import { createStore, applyMiddleware, compose } from 'redux';
import rootReducer from './reducers/index';
import { shuffle } from 'lodash/collection';
import { setEmptyState, setCol, setRow } from './helpers/tile';

const PUZZLE_SIZE = 4;

export const defaultState = {
  size: PUZZLE_SIZE,
  finished: false,
  tilesLoaded: 0,
  loaded: false,
  tiles: initTiles()
};

const enhancers = compose(
  window.devToolsExtension ? window.devToolsExtension() : f => f
);

const store = createStore(rootReducer, defaultState, enhancers);

function initTiles() {
  let ar = [];
  let total = PUZZLE_SIZE * PUZZLE_SIZE;

  for (let i = 0; i < total; i++) {
    ar[i] = {
      value: i,
      isEmpty: setEmptyState(i, total),
      position: i,
      col: setCol(i, PUZZLE_SIZE),
      row: setRow(i, PUZZLE_SIZE)
    };
  }

  ar = shuffle(ar)
  ar = ar.map((item, i) => {
    item.col = setCol(i, PUZZLE_SIZE);
    item.row = setRow(i, PUZZLE_SIZE);
    item.position = i;
    
    return item;
  })

  return ar;
}

export default store;


