import { findIndex } from 'lodash/array';
import { sortBy } from 'lodash/collection';
import { createSelector } from 'reselect';
import { setCol, setRow } from './tile';

const tiles = state => state.tiles;

export const getEmpty = createSelector(
  tiles,
  items => {
    return findIndex(items, (tile) => { return tile.isEmpty; });
  }
)

export const getIndexByPosition = (tiles, position) => {
  let value = tiles.length - 1;
  let empty = findIndex(tiles, (tile) => { return tile.position === position; });
  return empty;
}

export const swapClicked = (arr, index1, index2, size) => {
  let results = arr.map((val, i, ar) => {
    let tile = { ...val };

    if (i === index1) {
      tile.position = ar[index2].position;
    }

    if (i === index2) {
      tile.position = ar[index1].position;
    }

    tile.col = setCol(tile.position, size);
    tile.row = setRow(tile.position, size);

    return tile
  })

  return results;
}


export const solve = ({tiles, size}) => {
  var result = {
    solved:true
  };

  result.tiles = tiles.map((item,i)=>{
    item.position = item.value;
    item.col = setCol(item.position, size);
    item.row = setRow(item.position, size);

    if (item.position !== item.value) {
      result.solved = false;
    }
    
    return item;
  })

  return result;
}

export const isLoaded = (tiles, tilesLoaded) => {
  if (tilesLoaded >= tiles.length - 1) {
    return true;
  }
  return false;
}

export const canMove = ({ size, tiles, position, row, col })=> {
  let above = undefined;
  let below = undefined;
  let before = undefined;
  let next = undefined;

  const checkAbove = (item)=>{
    if (row > 0) {
      if (item.position === position - size) {
        above = item.isEmpty;
      }
    }
  }

  const checkBelow = (item)=> {
    if (row < size - 1) {
      if (item.position === position + size) {
        below = item.isEmpty;
      }
    }
  }

  const checkBefore = (item) => {
    if (col > 0) {
      if (item.position === position - 1) {
        before = item.isEmpty;
      }
    }
  }

  const checkNext = (item) => {
    if (col < size - 1) {
      if (item.position === position + 1) {
        next = item.isEmpty;
      }
    }
  }

  const checkItem = (item) => {
    checkAbove(item);
    checkBelow(item);
    checkBefore(item);
    checkNext(item);
    return item;
  }

  tiles.filter(checkItem);

  if (above || below || before || next) {
    return true;
  }

  return false;
}



