import '../styles/components/Puzzle.css';
import PropTypes from 'prop-types';
import React from 'react';
import { TweenMax } from 'gsap';
import { canMove } from '../helpers/tiles';
import Tile from './Tile.jsx';

class Puzzle extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      startTilesIntro: false
    };

    this.checkIfLoaded = this.checkIfLoaded.bind(this);
  }

  componentDidMount() {
    this.tween = TweenMax.to(this.el, 1, { rotation: '+=180', ease: Expo.easeOut, onComplete: this.checkIfLoaded });
  }

  render() {
    return (
      <div className='puzzle-wrapper'>
        <a ref={element => this.btnSolve = element} className='btn-solve intro' onClick={this.props.solve}> solve </a>
        <div ref={(element) => this.el = element} className={'puzzle'}>
          <div className='tiles-wrapper'>
            <img ref={(element)=> { this.fullImage = element}} alt='tile' className='fullimage' src='./images/puzzle/full.jpg' />
            {
              this.props.tiles.map((tile, i) => {
                return <Tile {...tile} {...this.props}
                  startIntro={this.state.startTilesIntro}
                  tileLoaded={this.props.tileLoaded}
                  key={i}
                  moveTile={this.props.moveTile}
                  canMove={canMove}
                  size={this.props.size} />
              })}
          </div>{}
        </div>
      </div>
    )
  }

  checkIfLoaded() {
    if (this.props.loaded) {
      this.el.classList.add('intro');
      this.el.setAttribute('style', '');
      TweenMax.to(this.fullImage, 1, {opacity:1, ease:Expo.easeOut, delay:0.7});
      TweenMax.to(this.fullImage, 1, {opacity:0.3, ease:Expo.easeOut, delay:2});
      TweenMax.to(this.btnSolve, 0.5, { opacity: 1, ease: Expo.easeOut, delay: 4, onStart:()=>{ this.btnSolve.classList.remove('intro')}});
      setTimeout(() => {
        this.setState({ startTilesIntro: true });
      }, 2000);

    } else {
      this.tween.time(0);
    }
  }
}

Puzzle.propTypes = {
  size: PropTypes.number.isRequired,
  tiles: PropTypes.array.isRequired,
  finished: PropTypes.bool.isRequired,
  loaded: PropTypes.bool.isRequired,
  tilesLoaded: PropTypes.number.isRequired,
  tileLoaded: PropTypes.func.isRequired,
  moveTile: PropTypes.func.isRequired
}


export default Puzzle;


